import React from 'react';
import Comments from './Comments';

const Posts = ({feed,userlist,commentlist,showHideComment})=>{
	return(
		<div>
		<h2>Posts</h2>
		<div className="postContainer">
		{
		feed.map(post=>{
			let postedby = userlist.filter(user=>user.id===post.userId);
			let commentsInthisPost = commentlist.filter(comment=>comment.postId===post.id);
			let commentCount = commentsInthisPost?commentsInthisPost.length:0;
			return(
				<div key={post.id} className="postCard">
					<div className="postTitle">
						{`${post.title}`}
					</div>
					<div className="postBody">
						{`${post.body}`}
					</div>
						<div className="postedBy">{`By: ${postedby[0].name}`}</div>
						<div className="commentCount" onClick={showHideComment}>
							{`${commentCount} comments`}
						</div>
						<div className="commentsContainer" id="commentsContainer">
							{commentsInthisPost.map(comment=>{
								return (<Comments key={comment.id} thisComment={comment} />)
								})
							}
						</div>
				</div>
			)	
		})
		}
		</div></div>
	)
}

export default Posts;